<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="80"></th>
          <th width="80"></th>
          <th width="80"></th>
          <th width="170"></th>
          <th width="120"></th>
          <th width="160"></th>
          <th width="160"></th>
          <th width="160"></th>
        </tr>
      </thead>
      <tbody >
        <tr v-for="item in orderlist" :key="item.id">
          <td>{{item.create_at}}</td>
          <td>
            <span v-if="item.is_paid" class="text-success"></span>
            <span v-else></span>
          </td>
          <td class="">
            {{item.paid_date}}
          </td>
          <td class="">
            {{item.message}}
          </td>
          <td class="">
            {{item.payment_method}}
          </td>
          <td class="">
            <button class="btn btn-outline-leafGreen btn-sm"
            @click="openProductModal(item.products)"></button>
          </td>
          <td class="">
              <button class="btn btn-outline-leafGreen btn-sm"
              @click="openUserModal(item.user)"></button>
          </td>
          <td>
            <button class="btn btn-outline-leafGreen btn-sm"
              @click="openOrdersModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination :page-data="pagination" @changePage="getOrders"></Pagination>
    <!--  -->
    <div class="modal fade" id="userModal" tabindex="-1" role="dialog"
    aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLongTitle"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>{{tempUser.name}}</p>
            <p>E-mail{{tempUser.email}}</p>
            <p>{{tempUser.address}}</p>
            <p>{{tempUser.tel}}</p>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
    aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLongTitle"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div v-for="item in tempOrderProductlist" :key="item.id">
              <hr>
              <p>{{item.title}}</p>
              <p>{{item.qty}}{{item.unit}}</p>
              <hr>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="modal fade" id="orderModal" tabindex="-1" role="dialog"
    aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLongTitle"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <h4> {{tempOrder.create_at}}</h4>
            <div class="form-group mt-4">
              <h6></h6>
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                  v-model="tempOrder.is_paid" :true-value="true" :false-value="false"
                  id="is_paid">
                  <label class="form-check-label" for="is_paid">
                    
                  </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateOrders"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import $ from 'jquery';
import Pagination from './../Pagination';

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      orderlist: [],
      products: [],
      tempOrderlist: {},
      tempUser: {},
      tempOrderProductlist: [],
      pagination: {},
      tempOrder: {},
      isLoading: false,
    };
  },
  methods: {
    getOrders(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOM}/admin/orders?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.orderlist = response.data.orders;
        vm.isLoading = false;
        vm.pagination = response.data.pagination;
      });
    },
    openUserModal(user) {
      const vm = this;
      $('#userModal').modal('show');
      vm.tempUser = user;
    },
    openProductModal(products) {
      $('#productModal').modal('show');
      const vm = this;
      vm.tempOrderProductlist = [];
      Object.keys(products).forEach((element) => {
        vm.tempOrderProductlist.push({
          ...products[`${element}`].product,
          qty: products[`${element}`].qty,
        });
      });
    },
    openOrdersModal(orders) {
      $('#orderModal').modal('show');
      this.tempOrder = orders;
    },
    updateOrders() {
      const vm = this;
      vm.isLoading = true;
      const dateTime = Date.now();
      const timestamp = Math.floor(dateTime / 1000);
      if (vm.tempOrder.is_paid) {
        vm.tempOrder = {
          ...vm.tempOrder,
          paid_date: timestamp,
        };
      } else {
        vm.tempOrder = {
          ...vm.tempOrder,
          paid_date: '',
        };
      }
      console.log(vm.tempOrder);
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOM}/admin/order/${vm.tempOrder.id}`;
      this.$http.put(api, { data: vm.tempOrder }).then((response) => {
        console.log(response.data.success);
        if (response.data.success) {
          $('#orderModal').modal('hide');
          vm.getOrders();
          vm.isLoading = false;
          this.$bus.$emit('message:push', '', 'danger');
        } else {
          this.$bus.$emit('message:push', '', 'danger');
        }
      });
    },
  },
  created() {
    this.getOrders();
  },
};
</script>



